.statistics {
  width: 400px;
  text-align: center;
  background-color: #fff;
  border-radius: 10px;
  padding-top: 10px;
  margin-bottom: 20px;
}

.stat-list {
  display: inline-flex;
}

.item-stats {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 80px;
  height: 80px;
}
